@import url(https://fonts.googleapis.com/css2?family=Nerko+One&display=swap);
:root {
  --color-basic: #cfd8dc;
}
body {
  font-family: 'Nerko One', cursive, -apple-system, BlinkMacSystemFont,
    'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans',
    'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background-color: #cfd8dc;

  background-color: var(--color-basic);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1rem;
  margin: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.Profile_profile__2GPVN {
  display: inline-flex;
  align-items: flex-start;
  justify-content: space-between;
  padding: 25px;
  border: 1px #fff solid;
  border-radius: 50px;
  background: linear-gradient(
    135deg,
    rgba(0, 0, 0, 0.22),
    rgba(255, 255, 255, 0.25)
  );
  box-shadow: 12px 12px 16px 0 rgba(0, 0, 0, 0.25),
    -8px -8px 12px 0 rgba(255, 255, 255, 0.3);
  color: #546e7a;
}
.Profile_avatar__u_VH9 {
  box-shadow: inset 8px 8px 16px 0 rgba(0, 0, 0, 0.2),
    inset -8px -8px 16px 0 rgba(255, 255, 255, 0.4);

  width: 100px;
  padding: 5px;
  border-radius: 50%;
  display: block;
}

.Profile_stats__10nFT {
  padding: 0;
  margin-left: 10px;
  border-radius: 13px;
  box-shadow: 1px 1px 0px 0 rgba(0, 0, 0, 0.25),
    -1px -1px 1px 0 rgba(255, 255, 255, 0.3);
  background: linear-gradient(
    -45deg,
    rgba(0, 0, 0, 0.22),
    rgba(255, 255, 255, 0.25)
  );
}
.Profile_stats__10nFT > li {
  padding: 7px;
  justify-content: space-between;
  list-style-type: none;
}
.Profile_quantity__2oAN3 {
  display: block;
  text-align: right;
  color: #000000;
}

